import React, { useState } from 'react';
import './TenPM.css'; // create this stylesheet

function TenPM() {
  const [answer, setAnswer] = useState('');

  const handleChange = (e) => {
    setAnswer(e.target.value);
  };

  return (
    <div className="tenpm-page">
      <h2>It's 10pm. Do you know where your boyfriend is?</h2>
      <textarea
        value={answer}
        onChange={handleChange}
        placeholder="Tell me here if you're not going to come over"
        className="answer-box"
        rows={6}
        cols={60}
      />
    </div>
  );
}

export default TenPM;
